<template>
  <StatusBar/>
  <Login/>
  <NoMobile/>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import NoMobile from "./NoMobile.vue"
import StatusBar from "./StatusBar.vue"
import Login from "./Login.vue"

export default {
  name: "Home",
  components: {
    NoMobile,
    StatusBar,
    Login,
  },
  data() {
    return {
      counter: 0,
    };
  },
  methods: {
    ...mapMutations([]),
    now() {
      return new Date();
    },
  },
  computed: {
    /* 
      계산결과 이기 때문에 state 데이터처럼 고정 fix 
      데이터 저장공간 결과 
      데이터가 게시물 데이터가 1000개 있음.
      1000개의 데이터를 array를 찾는 시간이 걸림.
      연산결과를 computed에 저장을 해놓으면 됨. 
      return 퇴 뱉어준다. 축약해주면 된다. 
      store에 데이터를 가져다가 쓰는 방법 
    */
    name() {
      return this.$store.state.name;
    },

    ...mapState(["name", "age", "likes"]),

    now2() {
      return new Date();
    },
  },
};
</script>

