<template>
<!-- 
  no-mobile이라고 이름을 주어졌다. 
  Vue component도 똑같이 naming하면된다.  
  똑같다.
  div>span 이다 
-->
  <div class="no-mobile">
    <span>화면을 작게 조정해주세요 :)</span>
  </div>

</template>

<script>
export default {};
</script>

<style scoped>
/* 
id로 해주었기에 no-mobile이라고 해준다.
class도 된다 임마

(위치 조정 fixed, x, y, z / 사이즈 / background )
position 어떻게 할까 fixed 
left 0; top 0 으로 주어야 겠다. z-index: 99; 제일 앞에다가 두어야지

(사이즈 조정)
height: 100vh; width: 100vw;

(배경 조정)
background-color: var(--yellow); 미리 정한 색으로 해버리자. (동일한색)

(안의 내용 정렬flex justify-content align-items + 폰트 사이즈 )
안에 있는 span을 조정해보자 (정렬, 폰트 사이즈 )
display flex;로 바꾸어주고 block이 었을거야 이었잖아 
justify-content: center; 좌우정렬 align-items: center 상하정렬 

front-size: 32px이 짱이자. 

*/
.no-mobile {
  position: fixed; /* https://thrillfighter.tistory.com/480 */
  top: 0; /* 0,0 에 맞추는 것이라 생각하면 됨  */
  left: 0;
  z-index: 99; /* 제일 앞으로 가게 해라잉  */
  width: 100vw;
  height: 100vh; /* 100vh view port이다 화면의 크기에 맞게 해라 */
  background-color: var(--green);
  display: flex; /* flex로 아예 왼쪽으로 붙여버리고  */
  justify-content: center; /* justify-conent center로 줘버리고  좌우  정렬*/
  align-items: center; /* align-items center로 줘버려앙 완전  상하 정렬*/
  font-size: 32px; /* 폰트 사이즈는  32 가 적절하다. */
  color: rgba(255, 255, 255, 0.9);
}

/* 
미디어 쿼리는 화면(screen), 티비(tv), 프린터(print)와 같은 미디어 타입(media type)과 적어도 하나 이상의 표현식(expression)으로 구성됩니다. 
표현식은 width, height, color와 같은 미디어 특성(media feature)들을 이용하여 그 특성들의 상태에 따라 다른 스타일 시트를 적용할 수 있습니다. 
미디어 쿼리는 CSS3에 포함되어 있으며, 컨텐츠의 변경없이 주로 화면의 크기에 따라 스타일 시트를 달리하여 적절한 모양을 보여줄 수 있습니다.

@media screen and 
상태에 따라서 다른 디자인을 할 수 있도록 한다. 

media query를 통해서 카카오 앱도 만들다가 다른 것도 만들어보면 어떨까?
화면의 크기가 일정정도 커지게 되면 그에 맞게 화면도 전환하게 끔 만들어보자.
1개의 화면에서 큰화면으로 전환 URL은 똑같이 사용 하이브리드 가능할까? 궁금하다.
똑같은 컨텐츠로 되어있어야겠지? 


*/

@media screen and (max-width: 645px) {
  .no-mobile {
    display: none;
  }
}
</style>
