<template>
<!-- 
    1. 최상단의 class 이름은 파일이름으로 맞춘다.  
    2. status-bar라고 하고 그 아래의 column부분은 __ double undersocre를 써준다. 
    3. 하위에 있는 것들은 CSS로 조정이 가능하기에 같은 레벨이라면 똑같은 클래스를 써준다.
    4. 반복을 하고 싶으면 반복문을 써도된다. 
    5. 10개이상 100개 이상 만들어야할 때는 v-for을 써야하지만 정해져있는 부분들은 v-for같이 하지말고 그냥 HTML를 사용하자.

    모든 시작은 div이다.block을가지고 있다. 괜찮다.
