<style scoped>
/* 
.statue-bar {
    display: flex; 자식이 다 붙게 만든다. 상속이 되기 때문이다.
    justify-content: 모든 아이템을 center로 놓는다.
    padding: 상하는 0px, 좌우는 3px씩 주어서 안에 들어가는 것을 예쁘게 해준다.
    font-weight는 600이 적당하다. status-bar의 경우
    box-sizing은 border-box; 이건 사실 잘 모르겠다. 
    항상 부모부터 먼저 써준다. 

    display 아래 애들을 어떻게 붙게 하지? flex 일렬로 
    justify-cotent  그래 이제 붙었으니 애들의 위치를 어떻게하지? div를 어떻게 붙게하지 
    padding 그래 아래 3개의 div가 너무 겉으로 붙지 않게 하지?  
    font-weift 그래 자식들의 font는 보기 좋게 해야지 600이면된다 
    div 에 대해서 붙게 만드는 것이다. 
}

*/


.status-bar {
  display: flex; /* 자식이 다 붙음 */
  justify-content: center;
  padding: 0px 3px;
  font-weight: 600;
  box-sizing: border-box;
}


/* 
.status-bar__column 의 모든 width를 33%을 갖게 만든다. 
좋은 방법이다. 같은 class이름을 가졌기에 가능한 것이다. 
공통적인 부분을 먼저 써준다. 
*/
.status-bar__column {
  width: 33%;
}


/* 
.statud-bar__column:first-child
자식 자체를 또 flex로 한다. div 를 가질 수 있기 때문이다.
justify-conent: left로 되어있다 기본이 그렇다. 
div안에있는 span의 위치를 어떻게 조정하지 
first-child를 사용한다. 
*/
.status-bar__column:first-child {
  display: flex;
  align-items: center;
}

/* 
$(.status-bar__column).eq(0) 이렇게도 쓸수 있다. jquery로 
지금은 jquery시간이 아니니, 그냥하자. 
display flex로 해주고 
justify-contnet를 center로 해준다.
align-items: center로 항상 맞춰준다.
*/
.status-bar__column:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 
:last-child라고 한다. 
display felex
align-items: center라고 한다. 

*/
.status-bar__column:last-child {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}


/* 
margin을 0px 0px을 주는 이유를 잘 몰겠네 붙이려고 하는 것 같다. 
span과 span 사이에 간격을 없애주려고 하는 것이다. 

*/
.status-bar__column:last-child span {
  margin: 0px 0px;
}
</style>
